function Counter({ from, to }) {
  const nodeRef = useRef();

  useEffect(() => {
    const node = nodeRef.current;

    const controls = animate(from, to, {
      duration: 4,
      ease: [0.075, 0.82, 0.165, 1],
      onUpdate(value) {
        node.textContent = value.toFixed(0);
      }
    });

    return () => controls.stop();
  }, [from, to]);

  return <span ref={nodeRef} />;
}

export default function Home(props) {
    const [participationCount, setParticipationCount] = useState("000");
    useEffect(async () => {
      const userSum = await axios.get("https://api.sketchdev.kr/user/sum");
      setParticipationCount(userSum.data.sum);
    }, [participationCount]);

    useEffect(() => {
      window.Kakao.Link.createScrapButton({
        container: '#kakao-share' ,
        requestUrl: window.location.href,
      })
    }, []);

    return (
      <motion.div initial="inital" animate="enter" exit="exit" variants={{ exit: { transition: { staggerChildren: 0.1 }}}}>
        <main>
          <div className="quiz">
            <h2 className="quiz__title">개발자 캐치마인드</h2>
            <div className="quiz__content">
              <div className="quiz__content__image">
                <QuizImage />
              </div>
              <span className="participation">지금까지 <Counter from={0} to={participationCount}/>명이 참여했어요!</span>
              <Link to="/quiz" className="start"><input value="시작하기" type="button" className="btn__start" /></Link>
              <Link to="/draw" className="start startDraw"><input value="그림 그려보실래요?" type="button" className="btn__start btn__startDraw" /></Link>
              <div className="share">
                <div className="share__method">
                  <button id="kakao-share" className="share-wrapper">
                    <img className="kakao shareicon" src={kakao} />
                  </button>
                  <button className="share-wrapper" onClick={() => {
                    window.open(`https://www.facebook.com/sharer/sharer.php?u=${window.location.href}`, '페이스북에 공유하기', 'width=600,height=800,location=no,status=no,scrollbars=yes');
                  }}>
                    <img className="facebook shareicon" src={facebook} />
                  </button>
                  <CopyToClipboard text={window.location.href}
                    onCopy={() => alert("링크가 클립보드에 복사되었어요!")}
                  >
                    <button className="share-wrapper">
                      <img className="link shareicon" src={link} />
                    </button>
                  </CopyToClipboard>
                </div>
              </div>
              {/* <div className="community">
                <h4 className="community__text">커뮤니티</h4>
                <div className="community__list">
                  <div className="community__wrapper"><a href="https://open.kakao.com/me/sketchdev"><FontAwesomeIcon icon={faCommentDots} color={"white"} size="lg" /></a></div>
                  <div className="community__wrapper"><a href="https://www.instagram.com/sketchdevkr"><FontAwesomeIcon icon={faInstagram} color={"white"} size="lg" /></a></div>
                </div>
              </div> */}
            </div>
          </div>
          <Footer />
        </main>
      </motion.div>)
}
